import { useEffect, useState } from "react";
import { dbService, storageService } from "../fbase";
import Nweet from "components/Nweet";
import { v4 as uuidv4 } from "uuid";

const Home = ({ userObj }) => {
    const [nweet, setNweet] = useState("");
    const [nweets, setNweets] = useState([]);
    const [attachment, setAttachment] = useState("");

    // 
    const onSubmit = async (event) => {
        event.preventDefault();

        const attachmentUrl = "";

        if (attachmentUrl !== "") {
            //uuid  ( db )
            const attachmentRef = storageService
                .ref()
                .child(`${userObj.uid}/${uuidv4()}`);

            //putString =  url      
            const reponse = await attachmentRef.putString(
                attachment,
                "data_url"
            );

            //  = response.ref.getDownloadURL     .
            attachmentUrl = await reponse.ref.getDownloadURL();
        }

        await dbService.collection("nweets").add({
            text: nweet,
            createAt: Date.now(),
            creatorId: userObj.uid,
            attachmentUrl,
        });

        //
        setNweet("");
        setAttachment("");
    };

    const onChange = (event) => {
        event.preventDefault();
        const {
            target: { value },
        } = event;
        setNweet(value);
    };

    const onFileChange = (event) => {
        event.preventDefault();
        const {
            target: { files },
        } = event;
        const theFile = files[0];
        const reader = new FileReader();

        reader.onloadend = (finishedEvent) => {
            //onloadend      
            const {
                currentTarget: { result },
            } = finishedEvent;
            setAttachment(result);
        };
        //     
        reader.readAsDataURL(theFile);
    };

    const onClearAttachment = () => setAttachment("");

    useEffect(() => {
        dbService.collection("nweets").onSnapshot((snapshop) => {
            const newArray = snapshop.docs.map((document) => ({
                id: document.id,
                ...document.data(),
            }));
            setNweets(newArray);
        });
    }, []);

    return (
        <>
            <form onSubmit={onSubmit}>
                <input
                    value={nweet}
                    onChange={onChange}
                    type="text"
                    placeholder=""
                    maxLength={120}
                />
                <input type="file" accept="image/*" onChange={onFileChange} />
                <input type="submit" value="Nweet" />
                {attachment && (
                    <>
                        <img src={attachment} width="50px" height="50px" />
                        <button onClick={onClearAttachment}></button>
                    </>
                )}
            </form>
            <div>
                {nweets.map((nweet) => (
                    <Nweet
                        key={nweet.id}
                        nweetObj={nweet}
                        isOwner={nweet.creatorId === userObj.uid}
                    />
                ))}
            </div>
        </>
    );
};

export default Home;
